<template>
    <div class="wiki-forget-mian-warp">
        <div class="wiki-forget-mian">
            <div class="wiki-forget-mian-content">
                <span>用户名</span>
                <el-input v-model="username" label="用户名" class="wiki-forget-main-user" placeholder="请输入账号"></el-input>
                
                <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
                    <el-form-item
                        prop="email"
                        label="邮箱"
                        :rules="[
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                        ]"
                    >
                        <el-input v-model="dynamicValidateForm.email"></el-input>
                    </el-form-item>
                </el-form>

                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="新密码" prop="pass">
                        <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="checkPass">
                        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>

                <button class="wiki-forget-main-btn" @click="modifyPassword">确定</button>
            </div>
        </div>
    </div>  
</template>

<script>
import * as ajax from '@/api'
export default {
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请输入密码'));
            } else {
            if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.pass) {
            callback(new Error('两次输入密码不一致!'));
            } else {
            callback();
            }
        };
        return {
            dynamicValidateForm: {
            domains: [{
                value: ''
            }],
            email: ''
            },
            username:'',
            ruleForm: {
                pass: '',
                checkPass: '',
                age: ''
            },
            rules: {
                pass: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
            }
        };
    },
    methods: {
        modifyPassword(){
            var reg = new RegExp("^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$")

            if(this.ruleForm.pass == ''){
                alert('密码不能为空')
                return
            }else if(this.username == ''){
                alert('账号为空，请输入账号')
                return
            }else if(this.dynamicValidateForm.email == ''){
                alert('邮箱为空，请输入邮箱')
                return
            }else if(!reg.test(this.dynamicValidateForm.email)){
                alert('邮箱格式不对，请输入正确的邮箱！')
                return
            }else{
                ajax.forgetpassword(this.username,this.dynamicValidateForm.email,this.ruleForm.pass).then(resp=>{
                    if(resp.data.code){
                        this.resetSuccess()
                        this.$router.push('/login')
                    }else{
                        this.resetError()
                    }
                })
            }
        },

        resetSuccess() {
            this.$message({
                message: '密码重置成功',
                type: 'success'
            });
        },
        restError(msg){
            this.$message.error(msg,+'请重试');
        }
    }
}
</script>

<style lang='scss'>
    .wiki-forget-mian-warp{
        width: 100%;
        .wiki-forget-mian{
            width: 1280px;
            margin: 0 auto;
            &-content{
                width: 500px;
                height: 600px;
                margin: 0 auto;
                border: 1px solid #999;
                margin-top: 30px;
                padding: 40px 20px 0 20px;
                span:nth-child(1){
                    padding-left: 40px;
                }
                .wiki-forget-main-user{
                    margin-bottom: 22px;
                    margin-top: 5px;
                    display:inline-block;
                    width:354px;
                    margin-left: 10px;
                }
                .demo-dynamic{
                    .el-form-item__content{
                        // margin-left: 0 !important;
                        .el-input{
                            margin-top: 0;
                        }
                        .el-form-item{
                            .el-form-item__label::before{
                                display: none;
                            }
                        }
                    }
                    .el-form-item__label{
                        width: 96px !important;
                    }
                }
                .wiki-forget-main-btn{
                    width: 100px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                    background-color:#409EFF;
                    border: none;
                    border-radius: 5px;
                    margin-left: 205px;
                    margin-top: 35px;
                }
            }
        }
    }
</style>
